.checbox-custom {
    display: flex;
    align-items: center;
    position: relative;
    input[type="checkbox"] {
        opacity: 0;
        position: absolute;
        left: 0;
        width: 24px;
        height: 24px;
        z-index: 2;
        cursor: pointer;
        &:checked+span {
            background: var(--c-yellow);
            border-color: transparent;
            &:after {
                content: '';
                background-image: url("data:image/svg+xml,%3Csvg width='12' height='9' viewBox='0 0 12 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M11.7225 0.359458C12.0925 0.838735 12.0925 1.61581 11.7225 2.09504L6.66987 8.64053C6.29989 9.11982 5.70014 9.11982 5.33016 8.64053L0.277476 2.09504C-0.0924918 1.61581 -0.0924918 0.838735 0.277476 0.359458C0.647443 -0.119819 1.24729 -0.119819 1.61726 0.359458L6.00001 6.03706L10.3828 0.359458C10.7528 -0.119819 11.3525 -0.119819 11.7225 0.359458Z' fill='black'/%3E%3C/svg%3E%0A");
                width: 12px;
                height: 9px;
                position: absolute;
                transform: translate(-50%, -50%);
                left: 50%;
                top: 50%
            }
        }
    }
    span {
        background: #FFFFFF;
        border: 1px solid #E6E9EC;
        border-radius: 7px;
        width: 24px;
        height: 24px;
        margin-right: 9px;
        cursor: pointer;
        position: relative;
    }
    label {
        font-weight: 400;
        font-size: 15px;
        line-height: 20px;
        color: #000
    }
    svg {
        width: 25px;
        height: 25px;
        margin-right: 10px;
    }
    &[data-info] {
        &:after {
            content: '?';
            width: 10px;
            height: 10px;
            display: flex;
            align-items: center;
            justify-content: center;
            background: var(--c-yellow);
            border-radius: 50%;
            font-weight: 400;
            font-size: 7px;
            line-height: 21px;
            color: #000;
            transform: translate(4px, -8px);
            cursor: pointer;
        }
    }
}