.banner {
    margin-bottom: 60px;
    .swiper-slide {
        height: 510px;
        position: relative;
        padding-left: 167px;
        padding-top: 72px;
    }
    .swiper-pagination {
        left: 167px
    }
    @media (max-width:992px) {
        .swiper-slide {
            padding-left: 50px;
            position: relative;
            height: 100%;
            min-height: 450px;
            &:after {
                content: '';
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                background: #00000047;
                border-radius: 15px;
            }
            .banner-item {
                position: relative;
                z-index: 1;
            }
        }
    }
    @media (max-width:767px) {
        margin-bottom: 30px;
        .swiper-slide {
            padding: 30px;
            min-height: 250px;
        }
        .swiper-pagination {
            left: 30px
        }
    }
}

.banner-item {
    height: 100%;
    width: 583px;
    position: relative;
    @media (max-width: 767px) {
        width: 100%;
    }
}

.banner__background {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 15px;
    object-position: right;
}

.banner-item__title {
    font-weight: 700;
    font-size: 64px;
    line-height: 87px;
    margin-bottom: 19px;
    color: var(--c-yellow);
    @media (max-width: 992px) {
        font-size: 32px;
        line-height: 40px;
    }
    @media (max-width: 767px) {
        font-size: 24px;
        line-height: 16px;
    }
}

.banner-item__text {
    font-weight: 700;
    font-size: 36px;
    line-height: 49px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #FFFFFF;
    margin-bottom: 50px;
    @media (max-width: 992px) {
        font-size: 24px;
    }
    @media (max-width: 767px) {
        margin-bottom: 20px;
        line-height: 23px;
        font-size: 16px;
    }
}

.banner-item__link {
    box-shadow: 0px 4px 0px #1F252E;
    border-radius: 5px;
    background: var(--c-yellow);
    width: 200px;
    height: 60px;
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #000;
    display: flex;
    align-items: center;
    justify-content: center;
    --size: 18px;
    &:after {
        content: '';
        background-image: url("data:image/svg+xml,%3Csvg width='15' height='16' viewBox='0 0 15 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14.7071 8.70711C15.0976 8.31658 15.0976 7.68342 14.7071 7.29289L8.34315 0.928932C7.95262 0.538408 7.31946 0.538408 6.92893 0.928932C6.53841 1.31946 6.53841 1.95262 6.92893 2.34315L12.5858 8L6.92893 13.6569C6.53841 14.0474 6.53841 14.6805 6.92893 15.0711C7.31946 15.4616 7.95262 15.4616 8.34315 15.0711L14.7071 8.70711ZM0 9H14V7H0V9Z' fill='black'/%3E%3C/svg%3E%0A");
        width: var( --size);
        height: var( --size);
        background-size: cover;
        display: block;
        margin-left: 16px;
    }
    @media (max-width: 992px) {
        font-size: 14px;
        height: 40px;
        width: 150px;
        --size: 12px
    }
}