* {
    padding: 0;
    margin: 0;
    border: 0;
    outline: none;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

:focus,
:active,
:focus-visible {
    // outline: none;
}

html,
body {
    width: 100%;
    min-width: $min-width;
    height: 100%;
}

body {
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

input,
button,
textarea {
    font-family: inherit;
    color: inherit;
}

button:active {
    outline: none;
}

// input::-ms-clear {
//     display: none;
// }
input[type="submit"] {
    cursor: pointer;
}

button {
    color: inherit;
    cursor: pointer;
    background: inherit;
}

button::-moz-focus-inner {
    padding: 0;
    border: 0;
}

a {
    color: inherit;
}

a {
    text-decoration: none;
}

ul li {
    list-style: none;
    list-style-position: inside;
}

ol li {
    list-style-position: inside;
}

img,
svg {
    width: 100%;
    height: 100%;
    object-fit: contain;
    vertical-align: top;
}

svg {
    // fill: transparent;
    // stroke: transparent;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
    font-weight: inherit;
}